h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Montserrat', sans-serif;
}

/* Example styles for your specific layout */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.card {
  max-width: 300px;
}

.card-media {
  height: 0;
  padding-top: 56.25%;
  /* 16:9 aspect ratio */
}

.image-text-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 16px;
}

.card-media {
  width: 40px;
  height: 40px;
}

.text {
  margin-bottom: 16px;
}

/* Example media queries for responsiveness */
@media (min-width: 600px) {
  .container {
    flex-direction: row;
    justify-content: center;
  }

  .card {
    margin-right: 16px;
  }

  .image-text-wrapper {
    margin-bottom: 0;
  }

  .text {
    margin-bottom: 0;
  }
}

@media (min-width: 960px) {
  .container {
    max-width: 960px;
    margin: 0 auto;
  }
}